<script>
  import ToLeftArrow from './svg/ToLeftArrow.svelte';
  import {RANKS} from '../../constants.svelte';
  import RankIcon from '../common/RankIcon.svelte';

  export let userRank = 0;
  let centerRank = userRank;
  let i = 1;
  let blockButtons = false;

  const toLeft = () => {
    if (centerRank > 0) {
      centerRank -= 1;
    }
  };

  const toRight = () => {
    if (centerRank + 1 < RANKS.length) {
      centerRank += 1;
    }
  };

  let transition = true;

  $: getPositionClass = (id) => {
    const delta = centerRank - id;
    let blockNumber;
    if (delta > 5) {
      blockNumber = 5 + delta;
      if (blockNumber > 10) {
        blockNumber = 10;
      }
    } else {
      blockNumber = 5 - delta;

      if (blockNumber < 0) {
        blockNumber = 0;
      }
    }
    return `block_${blockNumber}`;
  };
</script>

<div class="wrapper">
    <ToLeftArrow handler={toLeft} style="z-index: 5" block={blockButtons}/>
    <div class="carousel">
        <div class="dashBlock"></div>
        <div class="ranksWrapper">
            {#each RANKS as {id, name} (id)}
                {#if id === centerRank}
                    <span class="rankName">{RANKS[centerRank].name}</span>
                {/if}
                <div class="iconWrapper {getPositionClass(id)}" class:transition>
                    <RankIcon rankId={id}/>
                </div>
            {/each}
        </div>
    </div>
    <ToLeftArrow rotate handler={toRight} style="z-index: 5" block={blockButtons}/>
</div>

<style>
  .wrapper {
    width: 100%;
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    height: 134px;
    grid-auto-rows: 100%;

    align-content: center;
    align-items: center;

    --small-rank-size: 55px;
    --big-rank-size: 68px;
    --small-gap: 44px;
    --middle-gap: 111px;
    --big-gap: 168px;
    --dark-filter: brightness(50%) grayscale(100%);
  }

  .carousel {
    width: 1285px;
    height: 100%;
    position: relative;
  }

  .ranksWrapper {
    position: relative;
    height: 100%;
    width: 100%;

    display: grid;
    align-items: center;
    align-content: center;
  }

  .iconWrapper {
    z-index: 1;
    opacity: 0;
    width: var(--small-rank-size);
    height: var(--small-rank-size);

    position: absolute;
  }

  .block_0,
  .block_1,
  .block_2,
  .block_8,
  .block_9,
  .block_10 {
    transform: scale(1);
  }


  .block_3,
  .block_4,
  .block_5,
  .block_6,
  .block_7 {
    transform: scale(1.236);
  }

  .block_0 {
    left: calc(-1 * (var(--small-rank-size) + var(--small-gap)));
    filter: var(--dark-filter);
  }

  .block_1 {
    left: 0;
    opacity: 1;
    filter: var(--dark-filter);
  }

  .block_2 {
    left: calc(var(--small-rank-size) + var(--small-gap));
    opacity: 1;
    filter: var(--dark-filter);
  }

  .block_3 {
    left: calc(2 * (var(--small-rank-size) + var(--small-gap)));
    opacity: 1;
  }

  .block_4 {
    left: calc(2 * (var(--small-rank-size) + var(--small-gap)) + var(--big-rank-size) + var(--middle-gap));
    opacity: 1;
  }

  .block_5 {
    left: calc(2 * (var(--small-rank-size) + var(--small-gap) + var(--big-rank-size)) + var(--middle-gap) + var(--big-gap));
    opacity: 1;
  }

  .block_6 {
    left: calc(2 * (var(--small-rank-size) + var(--small-gap) + var(--big-gap)) + var(--middle-gap) + 3 * var(--big-rank-size));
    opacity: 1;
  }

  .block_7 {
    left: calc(2 * (var(--small-rank-size) + var(--small-gap)) + var(--middle-gap) + (3 * (var(--big-rank-size) + var(--big-gap))));
    opacity: 1;
  }

  .block_8 {
    left: calc(2 * (var(--small-rank-size)) + var(--middle-gap) + (3 * (var(--big-gap) + var(--small-gap))) + (4 * var(--big-rank-size)));
    filter: var(--dark-filter);
    opacity: 1;
  }

  .block_9 {
    left: calc(2 * var(--middle-gap) + (3 * (var(--big-gap)) + (var(--small-rank-size))) + (4 * (var(--big-rank-size) + var(--small-gap))));
    filter: var(--dark-filter);
    opacity: 1;
  }

  .block_10 {
    left: calc(2 * var(--middle-gap) + (3 * (var(--big-gap) + (var(--small-rank-size)))) + (4 * (var(--big-rank-size))) + (5 * var(--small-gap)));
    filter: var(--dark-filter);
  }

  .transition {
    transition: left 500ms, opacity 500ms, tranform 500ms, filter 500ms;
  }

  .dashBlock {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 67px;
    border-bottom: 2px dashed #413C3C;
  }

  .rankName {
    background: #413C3C;
    border-radius: 8px;
    box-sizing: border-box;
    width: 98px;
