<script>
  import {getContext} from 'svelte';
  import {getCSSVariable} from '../../../utils.svelte';
  import Box from '../../Box/Box.svelte';
  import Button from '../../UI/Button/Button.svelte';
  import LinkButton from '../../UI/LinkButton/LinkButton.svelte';
  import Tooltip from '../../UI/Tooltip/Tooltip.svelte';
  import Card from './Card/Card.svelte';
  import {cardColors} from './cardColors.js';

  const {ranks, user, data} = getContext('globalContext');
  const currentRank = ranks.find((el) => el.rank.next_id == user.rank_id + 1);


  // Get random color
  let prevRandomIndex = -1;

  const getRandomColor = () => {
    const randomIndex = Math.floor(Math.random() * cardColors.length);

    if (randomIndex == prevRandomIndex) {
      return getRandomColor();
    } else {
      prevRandomIndex = randomIndex;
      return cardColors[randomIndex];
    }
  };

  // State
  $: currentSlide = 'slow';
  $: isSpinning = false;


  // tabs
  const changeTab = (tab) => {
    if (!isSpinning) {
      currentSlide = tab;
    }
  };
  // wheel
  $: rows = new Array(30).fill(currentRank.bonus[currentSlide].possible_rewards);
  $: style = '';

  const getBonus = (sum) => {
    const order = currentRank.bonus[currentSlide].possible_rewards;
    const position = order.indexOf(sum);

    //determine position where to land
    const card = 150 + 16;

    let landingPosition = (12 * rows[0].length * card) + card * position + card / 2;

    const randomize = Math.floor(Math.random() * 140) - (140 / 2);
    // const randomize = 0;


    landingPosition = landingPosition + randomize;

    const object = {
      x: Math.floor(Math.random() * 50) / 100,
      y: Math.floor(Math.random() * 20) / 100
    };

    style = `
        transition-timing-function: cubic-bezier(0, ${object.x}, ${object.y} ,1);
        transition-duration: 6s;
        transform: translateX(-${landingPosition}px);
      `;

    isSpinning = true;

    setTimeout(function () {
      style = `
          transition-timing-function: ease;
          transition-duration: 0s;
        `;

      const resetTo = -(position * card + randomize) - card / 2;
      style = `transform: translateX(${resetTo}px)`;

      isSpinning = false;
    }, 6 * 1000);
  };


  const onSpinWheel = () => {
    // Random number

    !isSpinning && getBonus(currentRank.bonus[currentSlide].possible_rewards[Math.floor(Math.random() * currentRank.bonus[currentSlide].possible_rewards.length)]);
  };
</script>

<div class="wrapper">
    <Box
            class="box"
            style={`
      --bg-color: ${getCSSVariable('--gradient-black')};
    `}
    >
        <div class="header">
            <div class="header__title">
                <h2 class="title">Бонус</h2>
                <Tooltip
                        description="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod"
                />
            </div>
            <div class="tab-headers">
                <div class="tab-header" class:active={currentSlide == 'fast'}>
                    <div class="tab-header__title" on:click={() => changeTab('fast')}>
                        20 мин
                    </div>
                </div>
                <span class="slash">/</span>
                <div class="tab-header active" class:active={currentSlide == 'slow'}>
                    <div class="tab-header__title" on:click={() => changeTab('slow')}>
                        24 часа
                    </div>
                </div>
            </div>
        </div>
        <div class="body">
            <div class="roulette">
                <div class="wheel" style={style}>
                    {#each rows as row}
                        <div class='row'>
                            {#each row as card, i}
                                <Card
                                        value={card}
                                        colors={cardColors[i % cardColors.length]}
                                />
                            {/each}
                        </div>
                    {/each}
                </div>
                <div class="selector"></div>
            </div>
            <div class="body__buttons">
                <Button
                        value="Получить"
                        handler={onSpinWheel}
                        style={`
            --bg-color: ${getCSSVariable('--gradient-yellow')};
            --text-color: ${getCSSVariable('--color-black')};
            --border-color: ${getCSSVariable('--border-yellow')};
          `}
                />
                <div class="links">
                    <LinkButton
                            href={data.vk_link}
                            image="/images/vk.svg"
                    />
                    <LinkButton
                            href="#"
                            image="/images/tg.svg"
                    />
                </div>

            </div>
        </div>
    </Box>
</div>

