<script>
  import { getContext } from "svelte";
  import { formatTime, getCSSVariable } from "../../../utils.svelte";
  import Button from "../../UI/Button/Button.svelte";
  import Coins from "../../UI/Coins/Coins.svelte";
  import Percent from "../../UI/Percent/Percent.svelte";
  import ProgressBar from "../../UI/ProgressBar/ProgressBar.svelte";
  import Tooltip from "../../UI/Tooltip/Tooltip.svelte";


  const { ranks, user } = getContext('globalContext');
  const currentRank = ranks.find((el) => el.rank.next_id == user.rank_id + 1);

  let time = currentRank.cashback.time_left;
  
  const timer = setInterval(() => {
    time -= 60;

    if (time <= 0) {
      time = 0
      clearInterval(timer);
    }
  }, 60000);
</script>

<div class="wrapper">
  <div class="header">
    <h2 class="title">Кэшбэк <span class="green">New</span></h2>
    <div class="header__column">
      <Percent
        value={currentRank.cashback.percent + '%'}
        style={`--bg-color: ${getCSSVariable('--gradient-blue')};`}
      />
      <Tooltip
        description="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod"
      />
    </div>
  </div>
  <div class="body">
    <div class="time">
      <div class="time__header">{formatTime(time, 'full')}</div>
      <ProgressBar
        --progress-percent={Math.floor((172800 - time) / 172800 * 100) + '%'}
        --color={getCSSVariable('--gradient-yellow')}
        --bg-color="rgba(255, 255, 255, 0.3)"
      />
    </div>
    <div class="cashback">
      <Coins value={currentRank?.cashback.reward} img="/images/database_yellow.svg"/>
      <Button
        value="Забрать"
        style={`
          --bg-color: ${getCSSVariable('--gradient-yellow')};
          --text-color: ${getCSSVariable('--color-black')};
          --border-color: ${getCSSVariable('--border-yellow')};
        `}
      />
    </div>
  </div>
</div>

