<script>
  import { getContext } from "svelte";
import { formatTime, getCSSVariable } from "../../../utils.svelte";
  import Box from "../../Box/Box.svelte";
  import HeaderDescription from "../../HeaderDescription/HeaderDescription.svelte";
  import Button from "../../UI/Button/Button.svelte";
  import Coins from "../../UI/Coins/Coins.svelte";
  import GoTo from "../../UI/GoTo/GoTo.svelte";
  import ProgressBar from "../../UI/ProgressBar/ProgressBar.svelte";

  const { ranks, user } = getContext('globalContext');
  const currentRank = ranks.find((el) => el.rank.next_id == user.rank_id + 1);
  let time = currentRank?.vip.time_left;

  const timer = setInterval(() => {
    time -= 60;

    if (time <= 0) {
      time = 0
      clearInterval(timer);
    }
  }, 60000);

</script>

<div class:isBlocked="{!currentRank?.vip.available}" class="wrapper">
  <Box
    class="wrapper__body"
    style={`
      --bg-color: ${getCSSVariable('--gradient-violet')};
      --border-color: ${getCSSVariable('--border-violet')};
    `}
  >
    <div class="bg"></div>
    <HeaderDescription
      header="Vip bonus"
      description="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud"
      textColor="rgba(255, 255, 255, 0.4)"
    />
    <div class="row">
      <div class="coins">
        <Coins value={currentRank?.vip.reward} img="/images/database_white.svg"/>
        <Button
          value="Забрать"
          style={`
            --bg-color: ${getCSSVariable('--gradient-yellow')};
            --text-color: ${getCSSVariable('--color-black')};
            --border-color: ${getCSSVariable('--border-yellow')};
          `}
        />
      </div>
      <div class="time">
        <div class="time__header">{formatTime(time, 'short')}</div>
        <ProgressBar 
          --progress-percent={Math.floor((172800 - time) / 172800 * 100) + '%'}
          --color={getCSSVariable('--gradient-yellow')}
          --bg-color="rgba(255, 255, 255, 0.3)"
        />
      </div>
    
    
    </div>
  </Box>

</div>

