<script>
  import { getContext } from "svelte";
  import { getCSSVariable } from "../../../utils.svelte";
  import Box from "../../Box/Box.svelte";
  import Button from "../../UI/Button/Button.svelte";
  import LinkButton from "../../UI/LinkButton/LinkButton.svelte";
  import Tooltip from "../../UI/Tooltip/Tooltip.svelte";

  const { ranks, user, data } = getContext('globalContext');
  const currentRank = ranks.find((el) => el.rank.next_id == user.rank_id + 1);


  const getFormattedMoneyValue = (moneys, minutes) => {
    let word = 'монет';

    if (moneys == 1) {
      word = 'монета';
    } else if (moneys >= 2 && moneys <= 4) {
      word = 'монеты';
    }

    return `${moneys} ${word} в ${minutes} минут`;
  }
</script>

<div class="wrapper">
  <Box
    class="box"
    style={`
      --bg-color: ${getCSSVariable('--color-black')};
      --border-color: ${getCSSVariable('--gradient-black')};
    `}
  >
    <div class="header">
      <div class="bg"></div>
      <h2 class="title">Безлимит</h2>
      <Tooltip
        description="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod"
      />
    </div>
    <div class="row">
      <div class="img toll">
        <img src="/images/toll.svg" alt="toll">
      </div>
      <p class="text">{getFormattedMoneyValue(currentRank.unlim.sum, currentRank.unlim.per)}</p>
    </div>
    <div class="row">
      <div class="img conditions">
        <img src="/images/conditions.svg" alt="conditions">
      </div>
      <p class="text">Условие подписка на группу ВК</p>
    </div>
    <div class="buttons">
      <Button
        value="Получить"
        style={`
          --bg-color: ${getCSSVariable('--gradient-yellow')};
          --text-color: ${getCSSVariable('--color-black')};
          --border-color: ${getCSSVariable('--border-yellow')};
        `}
      />
      <LinkButton
        href={data.vk_link}
        image="/images/vk.svg"
      />
    </div>
  </Box>
</div>

